import React, { Component } from 'react';
import {Container, Row, Col, Image} from 'react-bootstrap';
import { CSSTransition } from 'react-transition-group';

export default class Section extends Component {
    constructor(props) {
      super(props);
      this.state = {
        isShow: false,
        y_position: 0
      };
      this.nodeRef = React.createRef();
    }
  
    componentDidMount() {
      this.element_obj = document.getElementById(this.props.id);
      //this.element_obj.style.visibility = "hidden";
      this.setState({
        y_position: Math.floor(this.element_obj.getBoundingClientRect().top+window.pageYOffset),
        isShow: false
      });
      window.addEventListener('scroll', this.handleScroll);
    }
  
    handleScroll = () => {
      if (this.state.isShow === false && 
        window.scrollY >= this.state.y_position - 20 && 
        window.scrollY <= this.state.y_position + this.element_obj.getBoundingClientRect().height - 20) {
        this.fadeIn();
      }
      else if (this.state.isShow === true && 
        ((Math.ceil(window.scrollY) < this.state.y_position - 20) ||
        (Math.ceil(window.scrollY) > this.state.y_position + this.element_obj.getBoundingClientRect().height - 20))) {
          this.fadeOut();
      }
    }
  
    fadeIn = () => {
      this.setState({isShow: true});
    }
  
    fadeOut = () => {
      this.setState({isShow: false});
    }
    
    render() {
      let title = this.props.name != null 
        ? <Row className="font-weight-bold mb-1" style={{fontSize: 24}}>{this.props.name}</Row>
        : <></>;
      let bgImage = this.props.bg != null
        ? <Row className="position-relative" style={{top: 0, left: 0, zIndex: -1, opacity: this.props.opacity}}><Image src={this.props.bg} height="100%"/></Row>
        : <></>;
      let content = this.props.children != null
        ? <Row className="justify-content-start text-justify">{this.props.children}</Row>
        : <></>;
      return(
        <>
        <Row id={this.props.id} className="min-vh-100 p-5 justify-content-center overflow-hidden">
            <Col lg={10} md={10} sm={12} xs={12} ref={this.nodeRef} className="text-left align-self-center">
              {title}
              {content}
            </Col>
        </Row>
        <hr className="mx-5"/>
        </>
      );
    }
}
  
