import React, { Component } from 'react';
import Section from './section';
import {Table} from 'react-bootstrap';
import achievements_list from '../data/achievements.json';
import ms from '../image/ms.png';

export default class MyAchievements extends Component {
  constructor(props) {
    super(props);
    this.state = {UIresponsed: false};
  }

  componentDidMount() {
    this.responseByUI().then(()=>{this.setState({UIresponsed: true})});
  }

  responseByUI = async () => {
    this.achieveHistory = await window.matchMedia('(max-width: 768px)').matches === true
    ? (<Table striped bordered variant="light" style={{background: 'rgba(255, 255, 255, 0.6)'}}>
        <thead>
          <tr><th style={{width: 200}}>
              Year,<br/>
              Event,<br />
              Organization
          </th></tr>
        </thead>
        <tbody>
          {achievements_list.map((achievements_items, index) => {
              return(
                <tr key={index}><td>
                  {achievements_items.year}<br/>
                  <b>{achievements_items.event}</b><br/>
                  <i>{achievements_items.organization}</i><br/>
                </td></tr>
              );
            })}
        </tbody>
      </Table>)
      : (<Table striped bordered variant="light" style={{background: 'rgba(255, 255, 255, 0.6)'}}>
      <thead>
        <tr>
          <th style={{width: '10%'}}>Year</th>
          <th style={{width: '60%'}}>Event</th>
          <th style={{width: '30%'}}>Organization</th>
        </tr>
      </thead>
      <tbody>
        {achievements_list.map((achievements_items, index) => {
          return(
            <tr key={index}>
              <td>{achievements_items.year}</td>
              <td>{achievements_items.event}</td>
              <td>{achievements_items.organization}</td>
            </tr>
          );
        })}
      </tbody>
    </Table>);
  }

  render() {
    return(
      <Section id="myachievements" name="Achievements" bg={ms} opacity="0.3">
        {this.achieveHistory}
      </Section>
    );
  }
}