import React, { Component } from 'react';
import {Container, Row, Col, Navbar, Nav, Image} from 'react-bootstrap';
import { CSSTransition } from 'react-transition-group';
import propic from '../image/propic.jpg';
import banner from '../image/banner3.jpeg';

export class Wrap extends Component {
    render() {
      return(
        <Container fluid id="wrap" className="p-0 m-0" style={{fontWeight: 330}}>
          {this.props.children}
        </Container>
      );
    }
}
  
export class Title extends Component {
    render() {
        return(
        <Row id="title" className="px-5 py-3" style={{background: '#e9ecef', fontWeight: 200}}>
          <Col lg={9} md={8} sm={6} xs={12} className="my-auto">
            <Row className="border-bottom border-dark mb-1" style={{fontSize: 35, lineHeight: 1.5}}>
                Mr. Chung Ka-ho Ryan <br />
                鍾嘉浩 <br />
            </Row>
            <Row style={{fontSize: 18}}>
                B.Sc. in Computer Science, Year 3 (2020-2021)<br />
                The Chinese University of Hong Kong
            </Row>
          </Col>
          <Col lg={3} md={4} sm={6} xs={12} className="my-auto">
            <Image src={propic} thumbnail alt="mypic" />
          </Col>
        </Row>
        );
    }
}
  
export class ContentNav extends Component {
    constructor(props) {
      super(props);
    }
  
    componentDidMount() {
      this.element_obj = document.getElementById('contentnav');
      this.element_space_obj = document.getElementById('contentnav_space');
      window.addEventListener('resize', this.handleResize);
    }
  
    handleResize = () => {
      this.element_obj.style.left = document.getElementById("title").getBoundingClientRect().left+window.pageXOffset+'px';
    }
  
    ScrollToContent = (div) => {
      let div_y_position = Math.ceil(document.getElementById(div).getBoundingClientRect().top+window.pageYOffset);
      console.log("to " + div + ": " + div_y_position);
      window.scrollTo({top: div_y_position, behavior: 'smooth'});
    }
  
    render() {
      return(
        <Row id="contentnav" className="position-sticky" style={{zIndex: '2', top: '0px', background: 'rgba(256, 256, 256, 0.9)', boxShadow: '0 5px 6px -6px rgba(0,0,0,.5)'}}>
          <Navbar expand="lg" variant="light" className="ml-4 py-0 w-100">
            <Navbar.Brand onClick={() => this.ScrollToContent('title')} style={{cursor: 'pointer'}}>My</Navbar.Brand>
            <Navbar.Toggle aria-controls="contentnav-control" />
            <Navbar.Collapse id="contentnav-control">
              <Nav className="mr-auto">
                <Nav.Link onClick={() => this.ScrollToContent('mybackground')}>Background</Nav.Link>
                <Nav.Link onClick={() => this.ScrollToContent('myprojects')}>Projects</Nav.Link>
                <Nav.Link onClick={() => this.ScrollToContent('myachievements')}>Achievements</Nav.Link>
                <Nav.Link onClick={() => this.ScrollToContent('mycontact')}>Contact</Nav.Link>
              </Nav>
            </Navbar.Collapse>
          </Navbar>
        </Row>
      );
    }
}
  
export class MyBanner extends Component {
    render() {
      return(
        <Row id="mybanner" className="overflow-hidden" style={{maxHeight: '60vh'}}>
          <Image src={banner} ref={this.nodeRef} width="100%" className="z-index-1" />
        </Row>
      );
    }
}
