import React, { Component } from 'react';
import Section from './section';
import {Table, Button} from 'react-bootstrap';
import projects_list from '../data/projects.json';
import gear from '../image/gear.jpg';

export default class MyProject extends Component {
  constructor(props) {
    super(props);
    this.state = {UIresponsed: false};
  }

  componentDidMount() {
    this.responseByUI().then(()=>{this.setState({UIresponsed: true})});
  }

  responseByUI = async () => {
      this.projectHistory = await window.matchMedia('(max-width: 768px)').matches === true
      ? (<Table striped bordered variant="light" style={{background: 'rgba(255, 255, 255, 0.6)'}}>
        <thead>
          <tr><th style={{width: 200}}>
              Name,<br/>
              Platform,<br />
              Description, Url
          </th></tr>
        </thead>
        <tbody>
          {projects_list.map((projects_items, index) => {
              return(
                <tr key={index}><td>
                  {projects_items.name}<br/>
                  {projects_items.platform}
                  <p style={{textAlign: 'left', color: 'grey', fontStyle: 'italic'}}>developed using {projects_items.tool}</p>
                  {projects_items.description}<br/>
                  <p className="text-right mt-3 mb-0">
                    {projects_items.url!='' && 
                      <a href={projects_items.url} target="_blank" className="ml-auto text-decoration-none text-light">
                        <Button variant="secondary" size="sm">{projects_items.repository}
                        </Button>
                      </a>
                    }
                  </p>
                </td></tr>
              );
            })}
        </tbody>
      </Table>)
      : (<Table striped bordered variant="light" style={{background: 'rgba(255, 255, 255, 0.6)'}}>
      <thead>
        <tr>
          <th style={{width: 200}}>Name</th>
          <th style={{width: 220}}>Platform</th>
          <th>Description</th>
          <th>Url</th>
        </tr>
      </thead>
      <tbody>
        {projects_list.map((projects_items, index) => {
            return(
              <tr key={index}>
                <td className="text-left">{projects_items.name}</td>
                <td>{projects_items.platform}
                    <p style={{textAlign: 'left', color: 'grey', fontStyle: 'italic'}}>developed using {projects_items.tool}</p>
                </td>
                <td>{projects_items.description}</td>
                <td>{projects_items.url!='' && <a href={projects_items.url} target="_blank" className="text-decoration-none text-light"><Button variant="secondary" size="sm">{projects_items.repository}</Button></a>}</td>
              </tr>
            );
          })}
      </tbody>
    </Table>);
  }
  
  render() {
    return(
      <Section id="myprojects" name="Projects" bg={gear} opacity="0.3">
        {this.projectHistory}
      </Section>
    );
  }
}